html {
    scroll-behavior: smooth;
}

body {
    /* overflow: hidden; */
}

#section1 {
    height: 100vh;
}

#section2 {
    height: 100vh;
}

ul {
    list-style: none;
    padding: 0;
}

li:before {
    content: "\f00c"; /* FontAwesome Unicode */
    font-family: FontAwesome;
    display: inline-block;
    margin-left: -1.3em; /* same as padding-left set on li */
    width: 1.3em; /* same as padding-left set on li */
}

.second {
    visibility: hidden;
}

.third {
    visibility: hidden;
}

.fourth {
    visibility: hidden;
}

.fifth {
    visibility: hidden;
}

.sixth {
    visibility: hidden;
}

.slide-in-bottom{ animation:slide-in-bottom 1s cubic-bezier(.25,.46,.45,.94) both; visibility: visible; }

@keyframes slide-in-bottom{0%{transform:translateY(1000px);opacity:0}100%{transform:translateY(0);opacity:1}}

.slide-in-right{-webkit-animation:slide-in-right .5s cubic-bezier(.25,.46,.45,.94) both;animation:slide-in-right .5s cubic-bezier(.25,.46,.45,.94) both}

@-webkit-keyframes slide-in-right{0%{-webkit-transform:translateX(1000px);transform:translateX(1000px);opacity:0}100%{-webkit-transform:translateX(0);transform:translateX(0);opacity:1}}@keyframes slide-in-right{0%{-webkit-transform:translateX(1000px);transform:translateX(1000px);opacity:0}100%{-webkit-transform:translateX(0);transform:translateX(0);opacity:1}}

.NavigationButton {
    position: fixed;
    top: 5%;
    right: 5%;
    z-index: 99999;
    font-family: neuzeit-grotesk, sans-serif;
    font-weight: 700;
    color: inherit;
}

